.Searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14),
    0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.SearchFormButton {
  cursor: pointer;
  padding: 3px 20px 3px 20px;
  border-radius: 8px;

  margin-right: 10px;
}

.SearchFormButton:hover,
.SearchFormButton:focus {
  background-color: #fff;
}

.SearchFormInput {
  width: 400px;
  height: 40px;
  border-radius: 10px;
  font-size: 20px;
}

.SearchFormButtonLabel {
  font-size: 24px;
}
